"use client"
import { useWorkspace } from "@/context";
import { ElementProps } from ".";

function ElementProject({ element, ...props }: ElementProps) {
  const { projectDispatch } = useWorkspace()

  function onClick() {
    projectDispatch({
      type: "LOAD_DATA",
      payload: { element }
    })
  }

  return (
    <div
      onClick={onClick}
      className="!select-none relative group/board w-full h-44 flex flex-col justify-between border rounded-sm text-sm cursor-pointer ring ring-transparent hover:ring-blue-300/30 active:shadow-none overflow-hidden"
    >
      <div className="absolute  h-full w-full bg-blue-300/10 opacity-30 " style={{ background: element.style.background, backgroundSize: "200%" }}>
      </div>
      <div className=" py-1 w-full bg-white/20 backdrop-blur-md"></div>
      <div className="w-full h-10 bg-white/50 backdrop-blur-md flex items-center transition-all">
        <p className="px-2 w-full line-clamp-2 leading-3 font-semibold select-none text-[11px] m-auto text-neutral-800/70 transition-all">
          {element.title}
        </p>
      </div>
    </div>
  )
}

ElementProject.displayName = "ElementProject"
export default ElementProject