"use client "
import { useWorkspace } from "@/context"
import React from "react"
import Appbar from "./appbar"
import CreateProject from "./elements/utils/createProject"
import Element from "./elements"
import clsx from "clsx"
import ConfigElement from "./elements/utils/config"

function Workspace() {
  const { globalState, projectState } = useWorkspace()
  return (
    <section className="h-[100vh] flex flex-col justify-between">
      <Appbar />
      {
        !globalState.configMode &&
        globalState.workspace.content.length > 0 &&
        <div className={clsx("h-full max-w-screen-xl mx-auto w-full px-2 flex flex-col", projectState.project && "hidden")} >
          <span className="h-1/2 w-full">
            {
                globalState.workspace.content.length > 0 &&
                <section className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 py-6 gap-1 w-full">
                <span className="min-h-44">
                  <CreateProject dense />
                </span>
                    {globalState.workspace.content.sort((a, b) => (new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime())).map(project => project.type === "project" && (
                      <Element key={project.id + "root"} element={project} type={"project"} />
                ))}
              </section>
            }
            </span>
        </div>
      }

      {
        globalState.workspace.content.length == 0 &&
        !globalState.configMode
        && <span className="h-full max-w-screen-xl mx-auto w-full px-2 flex">
          <CreateProject />
        </span>
      }

      {
        projectState.project
        && !projectState.configMode
        && <Element element={projectState.project} type={"board"} />
      }

      {
        projectState.project
        && projectState.configMode
        && <ConfigElement element={projectState.project} />
      }
      {
        !projectState.project
        && globalState.configMode
        && <ConfigElement element={globalState.workspace} />
      }

      <Footer />
    </section>
  )
}

function Footer() {

  return (
    <nav className="select-none flex border-t text-neutral-200  items-center justify-end px-2 max-h-6 min-h-6 w-full ">
    </nav>
  )
}

Workspace.displayName = "Workspace"
export default Workspace