"use client"
import { WorkspaceProvider } from "@/context";
import Workspace from "./flowboard.workspace";
import { Toaster } from "./ui/toaster";
import { CountdownProvider } from "@/context/countdown";

function FlowBoard() {
  return (
    <WorkspaceProvider >
      <CountdownProvider>
        <Workspace />
        <Toaster />
      </CountdownProvider>
    </WorkspaceProvider>
  );
}

FlowBoard.displayName = "FlowBoard"
export default FlowBoard
export {
  FlowBoard
}