"use client"

import React from "react"
import { IElement } from "./interface"
import { globalReducer } from "./reducer/global"
import { GlobalReducerActions, GlobalState, ProjectActions, ProjectState } from "./reducer/interface"
import { projectReducer } from "./reducer/project"
import { v4 } from "uuid"
import { generateGradient } from "@/lib/colors"

type ContextProps = {
  globalState: GlobalState
  globalDispatch: React.Dispatch<GlobalReducerActions>
  projectState: ProjectState
  projectDispatch: React.Dispatch<ProjectActions>
  deselectProject: () => void
  selectProject: (element: IElement) => void
  deleteElement: (element: IElement) => void
  updateElement: (element: IElement, newElement: Partial<IElement>) => void
  addElement: (element: IElement) => void
  sync: () => void
  completeElementCycle: () => void
  setConfigMode: (value: boolean) => void
}

let initialState: GlobalState = {
  workspaceName: "new workspace",
  configMode: false,
  updatedAt: new Date(),
  workspace: {
    position: 0,
    parent: "root",
    summary: {
      parents: ["root"],
      child: []
    },
    type: "workspace",
    id: v4(),
    content: [],
    style: {
      background: generateGradient()
    },
    updatedAt: new Date(),
    project: "root"
  }
}

const initialProjectState: ProjectState = {
  configMode: false,

}

const WorkspaceContext = React.createContext<ContextProps>({
  globalDispatch: () => { },
  globalState: initialState,
  projectDispatch: () => { },
  projectState: initialProjectState,
  deselectProject: () => { },
  selectProject: () => { },
  deleteElement: () => { },
  updateElement: () => { },
  addElement: () => { },
  sync: () => { },
  completeElementCycle: () => { },
  setConfigMode: () => { }
})

export const WorkspaceProvider = ({ children }: { children: React.ReactNode }) => {
  const [globalState, globalDispatch] = React.useReducer(globalReducer, initialState)
  const [projectState, projectDispatch] = React.useReducer(projectReducer, initialProjectState)

  function deselectProject() {
    if (projectState.project) {
      sync()
    }
    projectDispatch({
      type: "LOAD_DATA",
      payload: {
        element: undefined
      }
    })
  }

  function setConfigMode(value: boolean) {
    if (projectState.project) {
      projectDispatch({
        type: "SET_CONFIG_MODE",
        payload: { value }
      })
    } else {
      globalDispatch({
        type: "SET_CONFIG_MODE",
        payload: { value }
      })
    }

  }

  function completeElementCycle() {
    projectDispatch({
      type: "MIGRATE_ELEMENT",
      payload: {
        element: {
          ...projectState.startedElement!,
          metadata: {
            ...projectState.startedElement?.metadata!,
            status: "FINISHED"
          }
        },
        to: projectState.startedElement?.metadata?.whenFinish?.migrateTo!
      }
    })
    if (projectState.project) {
      globalDispatch({
        type: "MIGRATE_ELEMENT",
        payload: {
          element: {
            ...projectState.startedElement!,
            metadata: {
              ...projectState.startedElement?.metadata!,
              status: "FINISHED"
            }
          },
          to: projectState.startedElement?.metadata?.whenFinish?.migrateTo!
        }
      })
    }
    projectDispatch({
      type: "REMOVE_STARTED_ELEMENT"
    })
  }

  function selectProject(element: IElement) {
    if (projectState.project) {
      sync()
    }

    globalDispatch({
      type: "LOAD_PROJECT",
      payload: {
        project: element
      }
    })

    projectDispatch({
      type: "LOAD_DATA",
      payload: {
        element
      }
    })
  }


  function deleteElement(element: IElement) {
    projectDispatch({
      type: "DELETE_ELEMENT",
      payload: {
        elementId: element.id,
        parentId: element.parent
      }
    })

    globalDispatch({
      type: "DELETE_ELEMENT",
      payload: {
        elementId: element.id,
        parentId: element.parent
      }
    })
  }


  function updateElement(element: IElement, newElement: Partial<IElement>) {
    projectDispatch({
      type: "UPDATE_ELEMENT",
      payload: {
        oldElement: element,
        newElement: newElement as IElement,
      }
    })

    globalDispatch({
      type: "UPDATE_ELEMENT",
      payload: {
        oldElement: element,
        newElement: newElement as IElement,
      }
    })
  }

  function addElement(element: IElement) {
    projectDispatch({
      type: "ADD_ELEMENT",
      payload: {
        parentId: element.parent,
        element: element,
      }
    })

    globalDispatch({
      type: "ADD_ELEMENT",
      payload: {
        parentId: element.parent,
        element: element,
      }
    })
  }

  function sync() {
    if (projectState.project) {
      globalDispatch({
        type: "LOAD_PROJECT",
        payload: {
          project: projectState.project
        }
      })
    }
  }

  return (
    <WorkspaceContext.Provider value={{
      globalState,
      globalDispatch,
      projectState,
      projectDispatch,
      selectProject,
      deselectProject,
      deleteElement,
      updateElement,
      addElement,
      sync,
      completeElementCycle,
      setConfigMode
    }}>
      {children}
    </WorkspaceContext.Provider>
  )
}


export const useWorkspace = () => {
  const context = React.useContext(WorkspaceContext)
  if (!context) throw new Error("you must define Board context before use Board hooks")
  return context
}